@import url('https://fonts.googleapis.com/css?family=Farsan|Lalezar');

body{
  font-family: 'Farsan', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 80px;
}

.App-header {
  background-color: #222;
  height: auto;
  padding: 25px;
  color: rgb(231,181,6);
  
  /* background-image: url("../css/celebrate-6-seasons.jpg"); */
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center; 
  background-size: 100% auto;
}
.App-header h2 {
  margin-bottom: 10px;
  margin-top: 10px;
  background-color: rgb(81,81,81,0.55);
  padding: 0.8rem 100px;
}

.App-intro {
  font-size: 1.5rem;
  font-family: 'Tinos', serif;  
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.des_seg{
  /* height: 500px */
}
.intro_img{
  width: 100%;
  max-width: 300px;
}
.theTitle{
  font-family: 'Lalezar', serif;
}

.section label {
  margin: 5px;
  font-size: 1.4rem;
}
.section input {
  font-size: 1.5rem;

}
.section input::-webkit-input-placeholder {
  color: grey;
}
.section input:-ms-input-placeholder {
  color: grey;
}
.section input::placeholder {
  color: grey;
}
.section {
  margin: 40px 0px;
}
.theOverview{
  margin-top: 40px;
}
.theDescription textarea{
  height: 250px;
  margin-top: 10px;
  font-size: 1.5rem;
}
.theDescription textarea::-webkit-input-placeholder {
  color: grey;
}
.theDescription textarea:-ms-input-placeholder {
  color: grey;
}
.theDescription textarea::placeholder {
  color: grey;
}
.itinerary_container {
  padding: .5rem;
  border: 1px solid #cacaca;
  background-color: #fefefe;
  -webkit-box-shadow: inset 0 1px 2px rgba(10,10,10,.1);
          box-shadow: inset 0 1px 2px rgba(10,10,10,.1);
  height: auto;
  position: relative;
}
.day {
  font-size: 1.5rem;
  display: block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: rgb(231,181,6); 
  padding: 1.5rem;
  position: absolute;
  top: -40px;
  left: -30px;
}
.add_btn {
  background-color: #cacaca;
  font-size: 1.1rem;
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 35%;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  position: absolute;
  top: 25px;
  right: 20px;
}
.add_btn:hover {
  -webkit-box-shadow: 0px 4px  5px rgba(10,10,10,.4);
          box-shadow: 0px 4px  5px rgba(10,10,10,.4); 
  border-radius: 10%;

}
.itinerary_container .remove_btn {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: black;
  color: wheat;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  cursor: pointer;
  
}
.close.icon {
  color: wheat;
  width: 21px;
  height: 21px;
}

.close.icon:before {
  content: '';
  position: absolute;
  top: 13px;
  left: 5px;
  width: 21px;
  height: 3px;
  background-color: currentColor;
  -webkit-transform: rotate(-45deg);
          -ms-transform: rotate(-45deg);
      transform: rotate(-45deg);
}

.close.icon:after {
  content: '';
  position: absolute;
  top: 13px;
  left: 5px;  
  width: 21px;
  height: 3px;
  background-color: currentColor;
  -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
      transform: rotate(45deg);
}




